import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    userData: { },
    editing: null
  },
  mutations: {
    saveUserData: (state) => {
      const parsed = JSON.stringify(state.userData);
      localStorage.setItem('userData', parsed);
    }, loadUserData: (state) => {
      if (localStorage.getItem('userData')) {
        try {
          state.userData = JSON.parse(localStorage.getItem('userData'));
          
          const yesterday = new Date();
          yesterday.setDate(yesterday.getDate() - 1);
          const oldDates = Object.keys(state.userData).filter(d => new Date(d) < yesterday)
          
          if (oldDates.length){
            for (let d in oldDates)
              delete state.userData[oldDates[d]];
            const parsed = JSON.stringify(state.userData);
            localStorage.setItem('userData', parsed);
          }
        } catch (e) {
          localStorage.removeItem('userData');
        }
      }
    }, editPost: (state, postDate) => {
      if (state.editing == postDate)
        state.editing = null;
      else
        state.editing = postDate;
    },
    savePost: (state, postData) => {
      Vue.set(state.userData, postData.date, { status: postData.status }); // adding props to object is not reactive by default
      state.editing = null;
    }
  },
  actions: {
    saveUserData: ({ commit }) => { commit('saveUserData') }
    , loadUserData: ({ commit }) => { commit('loadUserData') }
    , editPost: ({ commit }, postDate) => { commit('editPost', postDate) }
    , savePost: ({ commit }, postData) => { commit('savePost', postData); commit("saveUserData"); }
  },
  getters: {
    postData: (postdate) => { state.userData },
    editing: () => { state.editing }
  }
})