<template>
  <div id="app">
    <div :key="posts.length">
      <app-post v-for="p in posts" v-bind:key="p.date" class="post" :data="p" :userData="userData[p.date]" :onSave="saveUserData" />
    </div>
    <app-key />
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'

//const today = new Date();
let tmpDate = new Date();
let posts = [];
for(let i=0; i<4; i++){
  addRow();
}

function addRow(){
  for(let i=0; i<3; i++){
  posts.unshift({
    date: tmpDate.toLocaleString('en-US', { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' }),
    type: getType(tmpDate)
  })
  tmpDate.setDate(tmpDate.getDate() + 1);
  }
}

function getType(date){
  switch(date.getDay()){
    case 0:
      return "Lifestyle"
    case 1:
      return "DIY"
    case 2:
      return "Dashboard"
    case 3:
      return "Product"
    case 4:
      return "Skinny"
    case 5:
      return "Mini"
    case 6:
      return "Micro"
  }
}

export default {
  data() {
    return {
      posts
    }
  },
  mounted: function () {
    window.scrollTo(0,document.body.scrollHeight);
    this.loadUserData();
  },
  computed: {
    ...mapState(['userData'])
  },
  methods: {
    ...mapActions(['loadUserData', 'saveUserData'])
  },
}

window.onscroll = function() {
  if (window.scrollY <= 20) {
    addRow();
    window.scrollTo(0,293+60);
  }
};
</script>

<style>
  #app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    margin: 0 auto;
    color: #2c3e50;
    margin-top: 60px;
    margin-bottom: 60px;
    width: 935px;
  }
  .post{
    width: 293px;
    height: 293px;
    display: inline-block;
    margin-bottom: 28px;
    margin-right: 28px;
    background-color: rgb(222, 231, 236);
  } .post:nth-child(3n){
    margin-right: 0;
  }
</style>
